import React, { useEffect, useState } from "react";
import Layout from "../App_1";
import axios from "axios";
import { useParams } from "react-router-dom";
import TaskAuth from "./Admin_components/TaskAuth";

export default function Viewtask(props) {
  const params = useParams();
  const [state, setstate] = useState([]);
  const [Auth, setAuth] = useState([]);
  const [count, setcount] = useState(0);

  useEffect(() => {
    axios
      .get(`http://10.0.0.116:5000/task/${params.TaskID}`)
      .then((res) => {
        setstate(res.data);
      })
      .catch((err) => (document.body.innerHTML = err));
    axios
      .get(`http://10.0.0.116:5000/Auth/${params.TaskID}`)
      .then((res) => {
        setAuth(res.data);
        console.log(res.data);
        setcount(res.data.length);
      })
      .catch((err) => (document.body.innerHTML = err));
  }, []);

  const Hide_ = () => {
    if (document.getElementById("Summary").classList[0] === "hidden") {
      document.getElementById("Summary").classList.remove("hidden");
    } else {
      document.getElementById("Summary").classList.add("hidden");
    }
  };

  return (
    <>
      <Layout />
      <div>
        <div>
          <div className="mt-3">
            <div className="px-4 sm:px-8">
              <div className="">
                <a
                  href="/#/tasklist"
                  className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded mt-2 mb-2"
                >
                  Back
                </a>
                <div className="inline-block min-w-full shadow-md rounded-lg overflow-hidden bg-gray-900 mt-3">
                  <div className="m-5 flex">
                    <div>
                      <h1 className="text-md text-red-500 ">Short Description</h1>
                      <p className="text-white">{state.Short_description}</p>
                    </div>
                    <div className="ml-10">
                      <h1 className="text-md text-red-500" onClick={Hide_}>
                        Summary
                      </h1>
                      <p id="Summary" className="hidden text-white">
                        {state.Summary}
                      </p>
                    </div>
                  </div>
                  <div>
                    <h1 className="lg:text-left sm:text-left sm:ml-4 text-red-500  text-lg">
                      Task Requirments
                    </h1>
                    <div>
                      {Auth.map((r) => (
                        <TaskAuth
                          ID={r.ID}
                          Count={count}
                          Task_ID={r.Task_ID}
                          Type={r.Type}
                          MinValue={r.MinValue}
                          MaxValue={r.MaxValue}
                          ExptectedValue={r.ExptectedValue}
                          Questions={r.Questions}
                          Attachment={r.Attachment}
                          AttachmentPath={r.AttachmentPath}
                          Feedback={r.feedback}
                          approve={r.Approve_By}
                        />
                      ))}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
