import React, { useState } from "react";
import Layout from "../App_1";
import axios from "axios";
import moment from "moment";
import { MomentDate, getCurrentTimeDashboard__ } from "../Utils/Functions";

export default function Report() {
  let momentOne = moment(
    new Date(`${MomentDate()} 15:00:00`),
    "YYYY-MM-DD HH:mm:ss"
  );
  let momentTwo = moment(
    new Date(`${MomentDate()} ${getCurrentTimeDashboard__()}`),
    "YYYY-MM-DD HH:mm:ss"
  );
  let durationA = moment.duration(momentOne.diff(momentTwo));
  console.log(durationA.asMinutes());
  return (
    <div className="">
      <Layout />
      <div className="flex">
        <div class="max-w-sm m-2 flex-1 bg-green-600 rounded overflow-hidden shadow-lg">
          <Report1 title="enter room" time="9:00" />
        </div>
      </div>
    </div>
  );
}

function Report1({ title, time }) {
  let momentOne = moment(
    new Date(`${MomentDate()} 15:00:00`),
    "YYYY-MM-DD HH:mm:ss"
  );
  let momentTwo = moment(
    new Date(`${MomentDate()} ${getCurrentTimeDashboard__()}`),
    "YYYY-MM-DD HH:mm:ss"
  );
  let durationA = moment.duration(momentOne.diff(momentTwo));
  console.log(durationA.asMinutes());
  console.log(title, time);
  return (
    <div class="max-w-sm m-2 flex-1 bg-green-600 rounded overflow-hidden shadow-lg">
      <div class="px-12 py-10">
        <div class=" text-white text-xl text-center font-bold">Clean Room</div>
        <div class=" text-white text-xl text-center">8:09</div>
        <div class=" text-white text-xl text-center">9:00</div>
      </div>
    </div>
  );
}
